import { connect } from 'react-redux'
import MyApplicationButtons from 'Shared/components/JobOfferListElement/components/MyApplicationButtons'
import { respondInterviewInvitation } from 'Shared/ducks/jobOfferApplications'
import { InterviewInvitationStatuses } from 'Shared/constants/Interviews'
import { sendMessage } from 'Chat/ducks/classes/messages'
import { isAnyTimeSlotChosen, chosenTimeSlot, chosenTimeSlotId, hasApplicationAnyTimeSlots } from 'Shared/selectors/jobOfferApplicationTimeSlots'
import { getInterviewInvitationStatus } from 'Shared/selectors/jobOfferApplications'
import {REQUEST_CHANGE_TIME_SLOT} from 'Shared/constants/JobOfferApplicationTimeSlots'
import { getChannelByJobOfferApplicationChatId } from 'Chat/selectors/channels'
import { getJobOfferApplicationChatSignature } from 'Shared/selectors/jobOfferApplicationChats'
import * as R from 'ramda'
import { isCandidateProfileApproved } from 'Shared/selectors/user'
import { isExternalJobOffer } from 'Shared/selectors/jobOffers'

const mapStateToProps = (state, ownProps) => {
  const applicationId = R.pathOr('', ['application', 'id'], ownProps)
  const jobOfferId = R.pathOr('', ['application', 'jobOfferId'], ownProps)
  const chatId = R.pathOr('', ['application', 'chatId'], ownProps)

  return ({
    isAnyTimeSlotChosen: isAnyTimeSlotChosen(state, applicationId),
    hasApplicationAnyTimeSlots: hasApplicationAnyTimeSlots(state, applicationId),
    chosenTimeSlot: chosenTimeSlot(state, applicationId),
    isRespondAChangeRequest: chosenTimeSlotId(state, applicationId) === REQUEST_CHANGE_TIME_SLOT,
    invitationStatus: getInterviewInvitationStatus(state, applicationId),
    chatChannelSignature: getJobOfferApplicationChatSignature(state, chatId),
    channel: getChannelByJobOfferApplicationChatId(state, chatId),
    isChannelAlreadyCreated: R.not(R.isEmpty(chatId)),
    areChannelsFetched: state.chat.areChannelsFetched,
    applicationId: R.pathOr('', ['application', 'id'], ownProps),
    isCandidateApproved: isCandidateProfileApproved(state),
    isExternalJobOffer: isExternalJobOffer(state, jobOfferId)
  })
}

const mapDispatchToProps = (dispatch, ownProps) => {
  const applicationId = R.pathOr('', ['application', 'id'], ownProps)

  return ({
    onAcceptInterviewRequest: () => dispatch(respondInterviewInvitation(applicationId, InterviewInvitationStatuses.accepted)),
    onRejectInterviewRequest: () => dispatch(respondInterviewInvitation(applicationId, InterviewInvitationStatuses.rejected)),
    sendMessage: (channelSid, message) => dispatch(sendMessage(channelSid, message))
  })
}

export default connect(mapStateToProps, mapDispatchToProps)(MyApplicationButtons)



