import React from 'react'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import EmptyStateWeb from './EmptyState'
import EmptyStateMobile from './EmptyState.mobile'

const EmptyState = props => props.isMobile ? <EmptyStateMobile {...props} /> : <EmptyStateWeb {...props} />

export default withDeviceWidthDetector(EmptyState)



