import React, { Component, Fragment } from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import unescape from 'lodash/unescape'
import withTranslations from 'Shared/hoc/withTranslations'

import './JobOfferListElement.scss'
import { generateJobOfferURL } from 'Shared/helpers/string'
import { Link, withRouter } from 'react-router-dom'
import { SmallJustIconButton } from 'Shared/components/Button'
import OlivPlusSmartPopUpConnector from 'Candidate/connectors/OlivPlusSmartPopUpConnector/OlivPlusSmartPopUpConnector'
import { formatDate } from 'Shared/helpers/date'
import { formats } from 'Shared/constants/Date'

class JobOfferListElementMobile extends Component {
  getJobTitle = () => {
    const { t, jobOffer, jobOfferListCategory, hidePrivacy } = this.props
    const jobTitle = `${jobOffer.title || t('employer.jobOffers.untitled')}`
    const cardHeaderClass = cx({
      'job-offer-card-mobile__header': true,
      'job-offer-card-mobile__header-linked': jobOfferListCategory !== 'draft'
    })

    return jobOfferListCategory !== 'draft'
      ? <Link
        to={{
          pathname: generateJobOfferURL(this.props.jobOffer),
          state: { from: this.props.history.location.pathname }
        }}
      >
        <h3 title={jobTitle} className={cardHeaderClass}>
          {unescape(jobTitle)} {jobOffer.isPrivate && !hidePrivacy &&
          <i className='job-offer-card__private ion-ios-eye-off' />}
        </h3>
      </Link>
      : <h3 title={jobTitle} className={cardHeaderClass}>
        {unescape(jobTitle)} {jobOffer.isPrivate && !hidePrivacy &&
        <i className='job-offer-card__private ion-ios-eye-off' />}
      </h3>
  }

  onViewChatChannelClick = () => this.props.history.push(`/candidate/chat/${this.props.channel.sid}`)

  chatButton = () => {
    const { isChannelAlreadyCreated, channel, areChannelsFetched, applicationId, isExternalJobOffer } = this.props
    switch (true) {
      case isChannelAlreadyCreated && !areChannelsFetched:
        return (
          <SmallJustIconButton
            disabled
            loading
            className='job-offer-card__chat-btn'
          />
        )
      case isChannelAlreadyCreated && channel && channel.messagesCount > 0:
        return (
          <SmallJustIconButton
            className='job-offer-card__chat-btn active-button'
            onClick={this.onViewChatChannelClick}
          >
            <i className='ion-md-chatboxes' />
          </SmallJustIconButton>
        )
      default:
        return (
          <OlivPlusSmartPopUpConnector
            triggerButton={
              <SmallJustIconButton
                className='job-offer-card__chat-btn'
              >
                <i className='ion-md-chatboxes' />
              </SmallJustIconButton>
            }
            service={'message'}
            type={'cta'}
            actionId={applicationId}
            isExternalJobOffer={isExternalJobOffer}
          />
        )
    }
  }

  render () {
    const { t, jobOffer, buttons, body, organisation, applicationId, isCandidateApproved, isExternalJobOffer } = this.props
    const hasNoDetails = !(jobOffer.jobType || jobOffer.commitment || jobOffer.city || jobOffer.country)

    const detailsWrapperClass = cx({
      'job-offer-card-mobile__details-wrapper': true,
      'job-offer-card-mobile__details-wrapper--hidden': hasNoDetails
    })

    return (
      <div className='job-offer-card-mobile__wrapper'>
        <div className='job-offer-card-mobile-container'>
          <div className='columns'>
            <div className='column col-9'>
              <div className='job-offer-card-mobile__description'>
                {this.getJobTitle()}
                <span className='job-offer-card-mobile__organisation'>
                  {organisation.name}
                </span>
                <div className={detailsWrapperClass}>
                  {jobOffer.jobType && <span
                    className='job-offer-card-mobile__details'>{t(`employer.jobOffers.jobTypes.${jobOffer.jobType}`)} • {jobOffer.commitment && (t(`employer.jobOffers.commitmentTypes.${jobOffer.commitment}`))} </span>}
                  {
                    (jobOffer.city || jobOffer.country) && (
                      <p className='job-offer-card-mobile__details'>
                        {jobOffer.city}{(jobOffer.city && jobOffer.country) && ', '}{jobOffer.country}
                      </p>
                    )
                  }
                  <p className='job-offer-card-mobile__deadline'>
                    { `${t('common.expires')}: ${formatDate(jobOffer.expiresAt, formats.dashed)}` }
                  </p>
                </div>
              </div>
            </div>
            <div className='column col-3 job-offer-card-mobile__buttons'>
              {isCandidateApproved &&
              <Fragment>
                <OlivPlusSmartPopUpConnector
                  triggerButton={
                    <SmallJustIconButton
                      onClick={() => {
                      }}
                      className='job-offer-card__chat-btn'
                    >
                      <i className='icon-im-boost' />
                    </SmallJustIconButton>
                  }
                  service={'boost'}
                  type={'cta'}
                  actionId={applicationId}
                  isExternalJobOffer={isExternalJobOffer}
                />
                {
                  this.chatButton()
                }
              </Fragment>
              }
            </div>
          </div>
        </div>
        {
          body && (
            <div className='job-offer-card-mobile__wrapper-body'>
              { body(jobOffer) }
            </div>
          )
        }
        {
          buttons && (
            <div className='job-offer-card-mobile__wrapper-buttons'>
              {buttons(jobOffer)}
            </div>
          )
        }
      </div>
    )
  }
}

JobOfferListElementMobile.defaultProps = {
  hideExpirationDate: false,
  showOrganisationName: false
}

JobOfferListElementMobile.propTypes = {
  children: PropTypes.node,
  jobOffer: PropTypes.shape({
    title: PropTypes.string,
    jobType: PropTypes.string,
    commitment: PropTypes.string,
    city: PropTypes.string,
    country: PropTypes.string,
    organisation: PropTypes.shape({
      name: PropTypes.string
    })
  }),
  body: PropTypes.func,
  buttons: PropTypes.func,
  hideExpirationDate: PropTypes.bool,
  jobOfferListCategory: PropTypes.string,
  jobOfferApplicationsCounters: PropTypes.object
}

export default withRouter(withTranslations(JobOfferListElementMobile))



