import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withRouter } from 'react-router-dom'
import withTranslations from 'Shared/hoc/withTranslations'
import { InterviewInvitationStatuses } from 'Shared/constants/Interviews'
import InterviewRequestConnector from 'Chat/connectors/Components/InterviewRequest/InterviewRequestConnector'
import { FormModalMobile } from 'Shared/components/Modal'
import cx from 'classnames'
import { PrimaryButton } from 'Shared/components/Button'

class MyApplicationButtonsMobile extends Component {
  static propTypes = {
    jobOfferId: PropTypes.string.isRequired,
    invitationStatus: PropTypes.string.isRequired,
    onAcceptInterviewRequest: PropTypes.func.isRequired,
    onRejectInterviewRequest: PropTypes.func.isRequired
  }

  static defaultProps = {
    invitationStatus: InterviewInvitationStatuses.notInvited,
    onAcceptInterviewRequest: () => {},
    onRejectInterviewRequest: () => {},
    jobOfferId: ''
  }

  state = {
    isRespondingToInvitation: false,
    isRespondToInterviewModalOpen: false,
    confirmInterviewInvitationModal: () => {},
    invitationModalDisableConfirm: false
  }

  openRespondToInterviewModal = () => this.setState({ isRespondToInterviewModalOpen: true })
  closeRespondToInterviewModal = () => this.setState({ isRespondToInterviewModalOpen: false })

  componentDidUpdate (prevProps) {
    if (this.props.invitationStatus !== InterviewInvitationStatuses.invited && this.props.invitationStatus !== prevProps.invitationStatus) {
      this.closeRespondToInterviewModal()
    }
  }

  getConfirmFunction = confirmFunction => this.setState({ confirmInterviewInvitationModal: confirmFunction })
  getDisabledState = disabled => this.setState({ invitationModalDisableConfirm: disabled })

  getInterviewInvitationActionElement = (status) => {
    const { t, channel } = this.props
    switch (true) {
      case status === InterviewInvitationStatuses.accepted:
        return (
          <div className='job-offer-card-mobile__label'>
            <i className='icon-im-accept-outline job-offer-card__label-icon job-offer-card__label-icon--accepted' />
            {t('common.interviewAccepted')}
          </div>
        )
      case status === InterviewInvitationStatuses.rejected:
        return (
          <div className='job-offer-card-mobile__label'>
            <i className='icon-im-decline-outline job-offer-card__label-icon job-offer-card__label-icon--rejected' />
            {t('common.interviewDeclined')}
          </div>
        )
      case status === InterviewInvitationStatuses.invited:
        return (
          <div className='job-offer-card-mobile__my-application-buttons-invitation-respond'>
            <PrimaryButton className='job-offer-card__my-application-buttons-invitation-respond--button' small onClick={this.openRespondToInterviewModal}>{t('common.replyToInvitation')}</PrimaryButton>
            <FormModalMobile
              title={t('modals.replyToInterviewInvitation.title')}
              handleClose={this.closeRespondToInterviewModal}
              submitText={t('common.confirm')}
              handleSubmit={this.state.confirmInterviewInvitationModal}
              disableSubmit={this.state.invitationModalDisableConfirm}
              show={this.state.isRespondToInterviewModalOpen}
              showContact={false}
              closeOnSubmit={false}
              showCancel={false}
            >
              <InterviewRequestConnector closeModal={this.closeRespondToInterviewModal} getConfirmFunction={this.getConfirmFunction} getDisabledState={this.getDisabledState} channelSid={channel.sid} />
            </FormModalMobile>
          </div>
        )
      default:
        return null
    }
  }

  render () {
    const { invitationStatus, channel } = this.props
    const containerClass = cx({
      'job-offer-card-mobile__my-application-buttons': true,
      'job-offer-card-mobile__my-application-buttons--available': channel && channel.messagesCount > 0
    })

    return <div className={containerClass}>
      {
        invitationStatus !== InterviewInvitationStatuses.notInvited &&
        channel &&
        <div className='job-offer-card-mobile__my-application-buttons-invitation'>
          {this.getInterviewInvitationActionElement(invitationStatus)}
        </div>
      }
    </div>
  }
}

export default withRouter(withTranslations(MyApplicationButtonsMobile))



