import React, { useEffect, useState } from 'react'
import AdvertCampaignService from 'Shared/services/AdvertCampaignService'
import * as R from 'ramda'
import { isNotNil } from 'Shared/helpers/Ramda'
import cx from 'classnames'
import './AdvertCampaign.scss'

const advertCampaignService = new AdvertCampaignService()

function AdvertCampaign (props) {
  const { fetchActiveAdvertCampaign, banner, className } = props
  const [impressionSent, setImpressionStatus] = useState(false)
  const isBannerAvailable = isNotNil(banner)

  const setImpressionIfNeeded = async () => {
    if (isBannerAvailable && R.not(impressionSent)) {
      await advertCampaignService.viewAdvertCampaignBanner(R.propOr('', 'id', banner))
      setImpressionStatus(true)
    }
  }

  const handleBannerClick = () => {
    advertCampaignService.clickAdvertCampaignBanner(R.propOr('', 'id', banner))
    window.open(R.propOr('', 'trackingUrl', banner), '_blank')
  }

  useEffect(() => {
    fetchActiveAdvertCampaign()
  }, [])

  useEffect(() => {
    setImpressionIfNeeded()
  }, [banner])

  const containerClass = cx({
    'advert-campaign-banner__container': true,
    [className]: className
  })

  return isBannerAvailable && (
    <div className={containerClass}>
      <img onClick={handleBannerClick} src={R.propOr('', 'photoUrl', banner)} alt={R.propOr('', 'name', banner)} />
    </div>
  )
}

AdvertCampaign.defaultProps = {
  fetchActiveAdvertCampaign: () => {}
}

export default AdvertCampaign



