import React from 'react'
import withMobileDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import ProfileCompletionWeb from './ProfileCompletion'
import ProfileCompletionMobile from './ProfileCompletion.mobile'

const ProfileCompletion = props => props.isMobile ? <ProfileCompletionMobile {...props} /> : <ProfileCompletionWeb {...props} />

export default withMobileDeviceWidthDetector(ProfileCompletion)



