import React, { Component } from 'react'
import cx from 'classnames'
import { isArabicLayout } from 'Shared/helpers/layout'
import PropTypes from 'prop-types'
import ReactModal from 'react-modal'
import withTranslations from 'Shared/hoc/withTranslations'
import { Link } from 'react-router-dom'
import { PrimaryLightBorderedButton } from 'Shared/components/Button'
import InactiveMemberPricingImage from 'Shared/assets/images/illustrations/inactivePricingSectionLight.svg'
import './InactiveMemberPricingModal.scss'

class InactiveMemberPricingModal extends Component {
  render () {
    const { t, onCtaClick, ariaHideApp, show, handleClose } = this.props
    const portalClass = cx({ 'inactive-membership-modal__portal': true, 'main--arabic': isArabicLayout })

    return (
      <ReactModal
        portalClassName={portalClass}
        overlayClassName='inactive-membership-modal__overlay'
        bodyOpenClassName='modal-opened'
        isOpen={show}
        contentLabel='Modal'
        className='inactive-membership-modal'
        closeTimeoutMS={300}
        shouldCloseOnOverlayClick
        onRequestClose={handleClose}
        ariaHideApp={ariaHideApp}
      >
        <div onClick={handleClose} className='inactive-membership-modal__close'>
          <i className='icon-im-close' />
        </div>
        <div className='columns'>
          <div className='column col-7 col-xs-12'>
            <div className='inactive-membership-modal__content'>
              <div className='inactive-membership-modal__heading'>{t('modals.inactiveMembershipJobsPricing.title')}</div>
              <div className='inactive-membership-modal__description'>{t('modals.inactiveMembershipJobsPricing.description')}</div>
              <div className='inactive-membership-modal__cta'>
                <Link to='/pricing'>
                  <PrimaryLightBorderedButton onClick={onCtaClick}>
                    {t('modals.inactiveMembershipJobsPricing.cta')}
                  </PrimaryLightBorderedButton>
                </Link>
              </div>
            </div>
          </div>
          <div className='column col-5 col-xs-12'>
            <div className='inactive-membership-modal__image'>
              <img src={InactiveMemberPricingImage} />
            </div>
          </div>
        </div>
      </ReactModal>
    )
  }
}

InactiveMemberPricingModal.propTypes = {
  handleClose: PropTypes.func.isRequired,
  show: PropTypes.bool,
  onCtaClick: PropTypes.func
}

InactiveMemberPricingModal.defaultProps = {
  show: false,
  ariaHideApp: false,
  onCtaClick: () => {}
}

export default withTranslations(InactiveMemberPricingModal)



