import { connect } from 'react-redux'
import FailedPaymentNotification from 'Candidate/components/FailedPaymentNotification/FailedPaymentNotification'
import { hasFailedOlivPlusPayment, getOlivPlusTransactionId } from 'Shared/selectors/transactions'

const mapStateToProps = state => ({
  hasFailedPayment: hasFailedOlivPlusPayment(state),
  transactionId: getOlivPlusTransactionId(state)
})

export default connect(mapStateToProps)(FailedPaymentNotification)



