import React from 'react'
import PropTypes from 'prop-types'
import { pure } from 'recompose'
import { Trans } from 'react-i18next'
import Modal from 'Shared/components/Modal'
import withTranslations from 'Shared/hoc/withTranslations'
import { openIntercomWithNewMessage } from 'Intercom/components/IntercomComponent'
import InactiveMemberPricingModal from 'Employer/components/InactiveMemberPricingModal'
import { membershipErrors } from 'Shared/constants/ErrorCodes'
import LocationCustomConnector from '../../../Shared/connectors/LocationCustomConnector'

const PublishJobOfferError = ({ t, postJobOfferAlertCode, handleHide, isVisible, onCtaClick, phoneNumber }) => {
  const handleModalSubmit = () => {
    handleHide()
    if (typeof onCtaClick === 'function') return onCtaClick()
  }

  const supportPhoneNumber = phoneNumber

  return (
    postJobOfferAlertCode === membershipErrors.noJobPostsLeft || membershipErrors.featureUnavailable || membershipErrors.noPlan
      ? <InactiveMemberPricingModal
        show={isVisible}
        handleClose={handleHide}
        onCtaClick={handleModalSubmit}
      />
      : <Modal
        title={t(`modals.${postJobOfferAlertCode}.title`)}
        handleClose={handleHide}
        show={isVisible}
        handleSubmit={handleHide}
        submitText={t('common.okGotIt')}
        showContact={false}
        showCancel={false}
      >
        <div>
          <p>{t(`modals.${postJobOfferAlertCode}.description`)}</p>
          <Trans i18nkey={t(`modals.${postJobOfferAlertCode}.contact`)}>
            Need to post a job or require additional support? <a onClick={openIntercomWithNewMessage} className='launch-intercom__link'>Talk to our team</a> or
            call {{supportPhoneNumber}}.
          </Trans>
        </div>
      </Modal>
  )
}

PublishJobOfferError.propTypes = {
  postJobOfferAlertCode: PropTypes.string.isRequired,
  handleHide: PropTypes.func.isRequired,
  isVisible: PropTypes.bool.isRequired
}

export default pure(withTranslations(LocationCustomConnector(PublishJobOfferError)))



