export const formatPhoneNumber = number => number.replace(/\(?(\d{3})\)?[- ]?(\d{3})[- ]?(\d{3})/g, '$1 $2 $3')

export const formatNumericInputWithBoundary = boundary => value => value > boundary ? num => num - 1 + '+' : num => num

export const formatAfterFive = formatNumericInputWithBoundary(5)

export const parseNumericInput = parseLimit => num => num === parseLimit + '+' ? parseLimit + 1 : num

export const formatMaxValue = (maxValue) => {
  formatNumericInputWithBoundary(maxValue)
}



