/* eslint-disable camelcase */
import React from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import DashboardCard from 'Candidate/components/DashboardCard/DashboardCard'
import Label from 'Shared/components/FormElements/Label/Label'
import PrimaryButton from 'Shared/components/Button'
import { THEME_COLORS } from 'Shared/constants/UI'
import RejectMessageModal from 'Candidate/components/RejectMessageModal'

import './ProfileDashboardRejected.scss'

class ProfileDashboardRejected extends React.PureComponent {
  state = {
    isMessageModalOpen: false
  }

  closeMessageModal = () => this.setState({ isMessageModalOpen: false })

  openMessageModal = () => this.setState({ isMessageModalOpen: true })

  render () {
    const { t } = this.props

    return (
      <div className='profile-rejected-card'>
        <DashboardCard>
          <DashboardCard.Icon icon={'ion-md-information-circle'} color={THEME_COLORS.IRON} />
          <DashboardCard.Wrapper>
            <Label>{t('rejectedCandidateBox.title')}</Label>
            <div className='c-profile-dashboard-availability__text'>
              {t('rejectedCandidateBox.content')}
            </div>
          </DashboardCard.Wrapper>
          <PrimaryButton
            onClick={this.openMessageModal}
            small
          >
            {t('common.viewMessage')}
          </PrimaryButton>
        </DashboardCard>
        <RejectMessageModal
          closeMessageModal={this.closeMessageModal}
          isMessageModalOpen={this.state.isMessageModalOpen}
        />
      </div>
    )
  }
}

export default withTranslations(ProfileDashboardRejected)



