import React, { useState } from 'react'
import PropTypes from 'prop-types'
import pure from 'recompose/pure'
import withTranslations from 'Shared/hoc/withTranslations'
import Label from 'Shared/components/FormElements/Label/Label'
import { formatDate } from 'Shared/helpers/date'
import { formats } from 'Shared/constants/Date'
import { THEME_COLORS } from 'Shared/constants/UI'
import DashboardCard from 'Candidate/components/DashboardCard/DashboardCard'
import AvailabilityModalConnector from 'Candidate/connectors/AvailabilityModalConnector'
import './ProfileDashboardAvailability.scss'

const ProfileDashboardAvailability = ({ t, isCandidateAvailable, availableFrom, availableTo }) => {
  const [editModalVisibility, setEditModalVisibility] = useState(false)
  const openEditModal = () => setEditModalVisibility(true)
  const hideEditModal = () => setEditModalVisibility(false)

  const getComponentState = () => {
    switch (true) {
      case !availableFrom || !availableTo:
        return {
          text: t('common.notProvided'),
          color: THEME_COLORS.PRIMARY
        }
      case !isCandidateAvailable:
        return {
          text: t('common.updateAvailability'),
          color: THEME_COLORS.ERROR
        }
      case isCandidateAvailable:
        return {
          text: `${formatDate(availableFrom, formats.full)} - ${formatDate(availableTo, formats.full)}`,
          color: THEME_COLORS.PRIMARY
        }
    }
  }

  return (
    <DashboardCard>
      <DashboardCard.Icon icon={'ion-md-calendar'} color={getComponentState().color} />
      <DashboardCard.Wrapper>
        <Label>{t('common.availability')}</Label>
        <div className='c-profile-dashboard-availability__text'>
          {getComponentState().text}
        </div>
      </DashboardCard.Wrapper>
      <div onClick={openEditModal} className='button button--outline button--small'>
        {t('common.changeDates')}
      </div>
      <AvailabilityModalConnector
        show={editModalVisibility}
        onClose={hideEditModal}
      />
    </DashboardCard>
  )
}

ProfileDashboardAvailability.propTypes = {
  availableFrom: PropTypes.string,
  availableTo: PropTypes.string,
  isCandidateAvailable: PropTypes.bool
}

export default withTranslations(pure(ProfileDashboardAvailability))



