import React, { Component, Fragment } from 'react'
import PropTypes from 'prop-types'
import { withRouter, Link } from 'react-router-dom'
import { SmallButton, SmallJustIconButton, PrimaryButton } from 'Shared/components/Button'
import withTranslations from 'Shared/hoc/withTranslations'
import { InterviewInvitationStatuses } from 'Shared/constants/Interviews'
import { generateJobOfferURL } from 'Shared/helpers/string'
import Modal from 'Shared/components/Modal'
import InterviewRequestConnector from 'Chat/connectors/Components/InterviewRequest/InterviewRequestConnector'
import moment from 'moment'
import { Trans } from 'react-i18next'
import OlivPlusSmartPopUpConnector from 'Candidate/connectors/OlivPlusSmartPopUpConnector/OlivPlusSmartPopUpConnector'

class MyApplicationButtons extends Component {
  static propTypes = {
    jobOfferId: PropTypes.string.isRequired,
    invitationStatus: PropTypes.string.isRequired,
    onAcceptInterviewRequest: PropTypes.func.isRequired,
    onRejectInterviewRequest: PropTypes.func.isRequired
  }

  static defaultProps = {
    invitationStatus: InterviewInvitationStatuses.notInvited,
    onAcceptInterviewRequest: () => {},
    onRejectInterviewRequest: () => {},
    jobOfferId: ''
  }

  state = {
    isRespondingToInvitation: false,
    isRespondToInterviewModalOpen: false
  }

  openRespondToInterviewModal = () => this.setState({ isRespondToInterviewModalOpen: true })
  closeRespondToInterviewModal = () => this.setState({ isRespondToInterviewModalOpen: false })

  componentDidUpdate (prevProps) {
    if (this.props.invitationStatus !== InterviewInvitationStatuses.invited && this.props.invitationStatus !== prevProps.invitationStatus) {
      this.closeRespondToInterviewModal()
    }
  }

  onViewDetailsClick = () => this.props.history.push(generateJobOfferURL(this.props.jobOffer))

  getInterviewInvitationActionElement = (status) => {
    const { t, channel, chosenTimeSlot, isAnyTimeSlotChosen, isRespondAChangeRequest, hasApplicationAnyTimeSlots } = this.props
    switch (true) {
      case status === InterviewInvitationStatuses.accepted && isAnyTimeSlotChosen && !isRespondAChangeRequest:
        return (
          <div>
            <div className='job-offer-card__label job-offer-card__label--status job-offer-card__label--status--with-time-slot'>
              <i className='icon-im-accept-outline job-offer-card__label-icon job-offer-card__label-icon--accepted' />
              {t('common.accepted')}
            </div>
            <div className='job-offer-card__label--time-slot'>
              <i className='ion-md-time' />&nbsp;
              {moment(chosenTimeSlot.dateFrom).utc().format('ll')}&nbsp;
              {moment(chosenTimeSlot.dateFrom).utc().format('hh:mm:A')}&nbsp;-&nbsp;{moment(chosenTimeSlot.dateTo).utc().format('hh:mm:A')}
            </div>
          </div>
        )
      case status === InterviewInvitationStatuses.accepted:
        return (
          <div className='job-offer-card__label job-offer-card__label--status'>
            <i className='icon-im-accept-outline job-offer-card__label-icon job-offer-card__label-icon--accepted' />
            {t('common.accepted')}
          </div>
        )
      case status === InterviewInvitationStatuses.rejected:
        return (
          <div className='job-offer-card__label job-offer-card__label--status'>
            <i className='icon-im-decline-outline job-offer-card__label-icon job-offer-card__label-icon--rejected' />
            {t('common.rejected')}
          </div>
        )
      case status === InterviewInvitationStatuses.invited:
        return (
          <div className='job-offer-card__my-application-buttons-invitation-respond'>
            <PrimaryButton className='job-offer-card__my-application-buttons-invitation-respond--button' small onClick={this.openRespondToInterviewModal}>{t('common.replyToInvitation')}</PrimaryButton>
            <Modal
              title={t('modals.replyToInterviewInvitation.title')}
              handleClose={this.closeRespondToInterviewModal}
              show={this.state.isRespondToInterviewModalOpen}
              showContact={false}
              showCancel={false}
              showSubmit={false}
            >
              <p className='job-offer-card__my-application-buttons-invitation-respond--description'>{t('modals.replyToInterviewInvitation.description')}</p>
              <InterviewRequestConnector channelSid={channel.sid} />
              {
                !hasApplicationAnyTimeSlots &&
                  <Trans i18nKey='modals.replyToInterviewInvitation.noTimeSlotDescription' parent='p' className='job-offer-card__my-application-buttons-invitation-respond--description'>
                    Have questions? <Link to={`/candidate/chat/${channel.sid}`}>Chat</Link> with the employer to follow up and discuss this opportunity.
                  </Trans>
              }
            </Modal>
          </div>
        )
      default:
        return null
    }
  }

  onViewChatChannelClick = () => this.props.history.push(`/candidate/chat/${this.props.channel.sid}`)

  chatButton = () => {
    const { isChannelAlreadyCreated, channel, areChannelsFetched, applicationId, isExternalJobOffer } = this.props

    switch (true) {
      case isChannelAlreadyCreated && !areChannelsFetched:
        return (
          <SmallJustIconButton
            disabled
            loading
            className='job-offer-card__chat-btn'
          />
        )
      case isChannelAlreadyCreated && channel && channel.messagesCount > 0:
        return (
          <SmallJustIconButton
            className='job-offer-card__chat-btn active-button'
            onClick={this.onViewChatChannelClick}
          >
            <i className='ion-md-chatboxes' />
          </SmallJustIconButton>
        )
      default:
        return (
          <OlivPlusSmartPopUpConnector
            triggerButton={
              <SmallJustIconButton
                className='job-offer-card__chat-btn'
              >
                <i className='ion-md-chatboxes' />
              </SmallJustIconButton>
            }
            service={'message'}
            type={'cta'}
            actionId={applicationId}
            isExternalJobOffer={isExternalJobOffer}
          />
        )
    }
  }

  render () {
    const { t, invitationStatus, channel, applicationId, isCandidateApproved, isExternalJobOffer } = this.props
    return (
      <div className='job-offer-card__my-application-buttons'>
        {
          invitationStatus !== InterviewInvitationStatuses.notInvited &&
          channel &&
          <div className='job-offer-card__my-application-buttons-invitation'>
            {this.getInterviewInvitationActionElement(invitationStatus)}
          </div>
        }
        <div className='job-offer-card__my-application-buttons--chat-details'>
          {isCandidateApproved &&
          <Fragment>
            <OlivPlusSmartPopUpConnector
              triggerButton={
                <SmallJustIconButton
                  className='job-offer-card__chat-btn'
                >
                  <i className='icon-im-boost' />
                </SmallJustIconButton>
              }
              service={'boost'}
              type={'cta'}
              actionId={applicationId}
              isExternalJobOffer={isExternalJobOffer}
            />
            {
              this.chatButton()
            }
          </Fragment>
          }
          <SmallButton onClick={this.onViewDetailsClick}>{t('jobOffer.jobDetails.viewJobDetails')}</SmallButton>
        </div>
      </div>
    )
  }
}

export default withRouter(withTranslations(MyApplicationButtons))



