import React, { useState, useEffect } from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import Modal from '../../layouts/CareerPro/Components/Modal/Modal'
import { withRouter } from 'react-router'
import { setLocalData, getLocalData } from 'Shared/helpers/session'
import mixpanel from 'mixpanel-browser'

const CareerProModal = (props) => {
  const getCareerProModal = getLocalData('careerProModal')
  const [modalIsOpen, setModalIsOpen] = useState(false)

  const handleSubmit = () => {
    props.history.push('/career-pro')
    mixpanel.track("Button Click - Career Pro Modal - Get Started")
  }

  const closeModal = () => {
    setModalIsOpen(false)
    setLocalData('careerProModal', true)
    mixpanel.track("Button Click - Career Pro Modal - Close")
  }

  useEffect(() => {
    if (getCareerProModal === true) {
      setModalIsOpen(false)
    } else {
      setModalIsOpen(true)
    }
  }, [])

  return (
    <Modal
      title=''
      modalIsOpen={modalIsOpen}
      setIsOpen={setModalIsOpen}
      closeModal={closeModal}
      status={'careerpro'}
      heading={props.t('candidatesPage.homepageCareerpro.modal.heading')}
      des={props.t('candidatesPage.homepageCareerpro.modal.description')}
      handleSubmit={handleSubmit}
      submitText={'Get Started'}
      showSubmit={'true'}
      btnFullW='full_w'
      outsideClickClose={false}
    />
  )
}

export default withRouter(withTranslations(CareerProModal))


