import { connect } from 'react-redux'
import ProfileDashboardRating from 'Candidate/components/ProfileDashboardRating'
import { profileSteps } from 'Shared/ducks/user/candidate'
import RatingService from 'Shared/services/RatingService'
import { isCandidateProfileAutoApproved } from 'Shared/selectors/user'

const mapStateToProps = state => {
  const ratingService = new RatingService(state.user.details.assessmentRating, state.user.details.assessmentPoints)

  return ({
    profileCompletionStatus: state.user.details.profileCompletionStatus,
    rating: ratingService.overallAssessment,
    isAutoApproved: isCandidateProfileAutoApproved(state)
  })
}

const mapDispatchToProps = dispatch => ({
  profileSteps: () => dispatch(profileSteps())
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfileDashboardRating)



