import React from 'react'
import { Helmet } from 'react-helmet'
import PropTypes from 'prop-types'
import Notification from 'Shared/components/Notification'
import withTranslations from 'Shared/hoc/withTranslations'
import Container from 'Shared/components/Container'
import ProfileCompletionConnector from 'Candidate/connectors/ProfileCompletionConnector'
import ProfileDashboardRatingConnector from 'Candidate/connectors/ProfileDashboardRatingConnector'
import ProfileDashboardAvailabilityConnector from 'Candidate/connectors/ProfileDashboardAvailabilityConnector'
import MyApplicationsConnector from 'Candidate/connectors/Views/MyApplicationsConnector'
import ProfileDashboardRejected from 'Candidate/components/ProfileDashboardRejected'
import AvailabilityModalConnector from 'Candidate/connectors/AvailabilityModalConnector'
import { PageTopHeaderMobile } from 'Shared/components/PageTopHeader'
import { cleanseString, withPossessiveS } from 'Shared/helpers/string'
import FailedPaymentNotificationConnector from 'Candidate/connectors/FailedPaymentNotification/FailedPaymentNotificationConnector'
import UpdateSalaryExpectationNotificationConnector from 'Candidate/connectors/UpdateSalaryExpectationNotification/UpdateSalaryExpectationNotificationConnector'
import AdvertCampaignConnector from 'Shared/connectors/Components/AdvertCampaign/AdvertCampaignConnector'
import { BANNER_TYPES } from 'Shared/constants/advertCampaign'
import CareerProModal from '../../../Shared/components/CareerProModal/CareerProModal'
import CareerproBanner from '../../../Shared/components/CareerproBanner/CareerproBanner'
import bannerImg from 'Shared/assets/images/brand/banner.svg'

import './DashboardView.scss'
import mixpanel from 'mixpanel-browser'

class DashboardView extends React.Component {
  state = {
    isAvailabilityModalOpen: false,
    modalSeen: false
  }

  getLeftInfoBox = () => {
    switch (true) {
      case this.props.isCandidateRejected:
        return <ProfileDashboardRejected />
      case this.props.isCandidateApproved:
        return <ProfileDashboardRatingConnector />
      default:
        return <ProfileCompletionConnector />
    }
  }

  componentDidMount () {
    this.props.fetchUser()
    mixpanel.track("Page View - Dashboard");
  }

  componentDidUpdate () {
    const { userFetched, isCandidateAvailable, hasProvidedAvailability } = this.props
    if (userFetched && !isCandidateAvailable && hasProvidedAvailability && !this.state.modalSeen) {
      return this.setState({ isAvailabilityModalOpen: true, modalSeen: true })
    }
  }

  closeAvailabilityModal = () => this.setState({ isAvailabilityModalOpen: false })

  profileCompletionNotification = () => {
    const { t, isCandidateProfileComplete } = this.props

    return isCandidateProfileComplete && (
      <Notification
        messageText={t('notifications.candidate.approvedProfileVisibility')}
        iconClassName='icon-im-alert-circle-outline'
        className='dashboard-view__notification'
        warning
      />
    )
  }

  render () {
    const { t, userFetched, firstName } = this.props
    return (
      <div className='dashboard-view__container parsing--hidden'>
        <Helmet>
          <title>{firstName && cleanseString(t('meta.candidate.title.dashboard', { firstName: withPossessiveS(firstName) }))}</title>
          <meta property='og:title' content={firstName && cleanseString(t('meta.candidate.title.dashboard', { firstName: withPossessiveS(firstName) }))} />
        </Helmet>

        <CareerProModal />
        <PageTopHeaderMobile title={t('common.myApplications')} />
        <Container loading={!userFetched} loaderClassName={'loader--centered'}>
          <div className='dashboard-view'>
            {/* Careerpro Ads Banner */}
            <CareerproBanner
              name='dashboardNotify'
              heading='No more waiting around with CareerPro'
              des='Get that interview call in no time, by submitting expert written and edited Resumes, Cover letters and Linkedin Profile.'
              image={bannerImg}
              submitButtonText='Get Started'
              showSubmitButton
              hideCloseButton
            />
            <FailedPaymentNotificationConnector />
            <UpdateSalaryExpectationNotificationConnector />
            <div className='columns'>
              <div className='column col-6 col-md-12'>
                {this.getLeftInfoBox()}
              </div>
              <div className='column col-6 col-md-12'>
                <ProfileDashboardAvailabilityConnector />
              </div>
            </div>
            {this.profileCompletionNotification()}
            <div className='dashboard-view--applications'>
              <span className='dashboard-view--applications--title'>{t('common.myApplications')}</span>
              <MyApplicationsConnector />
              <AdvertCampaignConnector type={BANNER_TYPES.APPLICATION_LIST_BANNER} />
            </div>
          </div>
        </Container>
        <AvailabilityModalConnector
          show={this.state.isAvailabilityModalOpen}
          onClose={this.closeAvailabilityModal}
        />
      </div>
    )
  }
}

DashboardView.propTypes = {
  isCandidateRejected: PropTypes.bool,
  isCandidateRated: PropTypes.bool,
  profileCompletion: PropTypes.number,
  userFetched: PropTypes.bool,
  isCandidateAvailable: PropTypes.bool
}

export default withTranslations(DashboardView)



