import React from 'react'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import DashboardViewWeb from './DashboardView'
import DashboardViewMobile from './DashboardView.mobile'

const DashboardView = props => props.isMobile ? <DashboardViewMobile {...props} /> : <DashboardViewWeb {...props} />

export default withDeviceWidthDetector(DashboardView)



