import React from 'react'
import withMobileDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import ProfileDashboardRatingWeb from './ProfileDashboardRating'
import ProfileDashboardRatingMobile from './ProfileDashboardRating.mobile'

const ProfileDashboardRating = props => props.isMobile ? <ProfileDashboardRatingMobile {...props} /> : <ProfileDashboardRatingWeb {...props} />

export default withMobileDeviceWidthDetector(ProfileDashboardRating)



