import { connect } from 'react-redux'
import JobOfferListElement from 'Shared/components/JobOfferListElement'
import { getJobOfferById, isExternalJobOffer } from 'Shared/selectors/jobOffers'
import { getOrganisation } from 'Shared/selectors/organisations'
import { Roles } from 'Shared/constants/User'
import { getChannelByJobOfferApplicationChatId } from 'Chat/selectors/channels'
import * as R from 'ramda'
import { getApplicationByJobOfferId } from 'Shared/selectors/jobOfferApplications'
import { isCandidateProfileApproved } from 'Shared/selectors/user'
import { useApiGetAtsMatchScore } from 'Shared/services/api/ats'

import { getApplicationsForJobOffer } from 'Shared/ducks/jobOfferApplications'


const { apiGetAtsMatchScore } = useApiGetAtsMatchScore()


const mapStateToProps = (state, ownProps) => {
  const jobOffer = getJobOfferById(state, ownProps.jobOfferId)
  const application = getApplicationByJobOfferId(state, ownProps.jobOfferId)
  const chatId = R.pathOr('', ['chatId'], application)
  const applicationId = R.pathOr('', ['id'], application)
  return ({
    isCandidate: state.user.role === Roles.candidate.name,
    jobOffer: jobOffer,
    organisation: getOrganisation(state, jobOffer.organisationId),
    channel: getChannelByJobOfferApplicationChatId(state, chatId),
    isChannelAlreadyCreated: R.not(R.isEmpty(chatId)),
    areChannelsFetched: state.chat.areChannelsFetched,
    applicationId: applicationId,
    isCandidateApproved: isCandidateProfileApproved(state),
    isExternalJobOffer: isExternalJobOffer(state, ownProps.jobOfferId),
    
    apiGetAtsMatchScore: apiGetAtsMatchScore
  })
}

const mapDispatchToProps = (dispatch, ownProps) => ({

  getApplicationsForJobOffer: jobOfferId => dispatch(getApplicationsForJobOffer(jobOfferId))
})

export default connect(mapStateToProps, mapDispatchToProps)(JobOfferListElement)



