import { connect } from 'react-redux'
import MyApplicationsView from 'Candidate/views/MyApplicationsView'
import { fetchCandidatesApplicationsForDashboard } from 'Shared/ducks/jobOfferApplications'
import { getCandidatesAppliedJobOffers, getCandidatesViewedJobOffers, getCandidatesInterviewedJobOffers,
  getCandidatesHiredJobOffers, getCandidatesDeclinedJobOffers } from 'Shared/selectors/jobOffers'

const applicationsTabs = {
  applied: 'applied',
  viewed: 'viewed',
  interviewRequested: 'interviewRequested',
  hired: 'hired',
  declined: 'declined'
}

const mapStateToProps = state => {
  window.applied = getCandidatesAppliedJobOffers(state).length
  return ({
    [applicationsTabs.applied]: getCandidatesAppliedJobOffers(state),
    [applicationsTabs.viewed]: getCandidatesViewedJobOffers(state),
    [applicationsTabs.interviewRequested]: getCandidatesInterviewedJobOffers(state),
    [applicationsTabs.hired]: getCandidatesHiredJobOffers(state),
    [applicationsTabs.declined]: getCandidatesDeclinedJobOffers(state),
    applicationsTabs: applicationsTabs
  })
}

const mapDispatchToProps = dispatch => ({
  loadJobOffers: () => dispatch(fetchCandidatesApplicationsForDashboard())
})

export default connect(mapStateToProps, mapDispatchToProps)(MyApplicationsView)



