import React, { Fragment } from 'react'
import './EmptyState.scss'

const EmptyState = ({ image, title }) => (
  <Fragment>
    <div>{title}</div>
    <img src={image} alt='empty' className='job-offer-list__empty-state-image' />
  </Fragment>
)

export default EmptyState



