import { connect } from 'react-redux'
import { and } from 'ramda'
import DashboardView from 'Candidate/views/Dashboard'
import { isCandidateApproved, isCandidateRejected } from 'Shared/helpers/profile'
import { isStillAvailable } from 'Shared/helpers/date'
import { isCandidateProfileComplete } from 'Shared/selectors/user'
import { fetchUser } from 'Shared/ducks/user/candidate'

const mapStateToProps = state => {
  const { profileCompletionStatus, availableTo, availableFrom, firstName, profileCompletionPercentage } = state.user.details

  return ({
    profileCompletion: profileCompletionPercentage,
    userFetched: state.user.userFetched,
    isCandidateApproved: isCandidateApproved(profileCompletionStatus),
    isCandidateRejected: isCandidateRejected(profileCompletionStatus),
    isCandidateAvailable: isStillAvailable(availableTo),
    hasProvidedAvailability: and(availableTo, availableFrom),
    firstName: firstName,
    isCandidateProfileComplete: isCandidateProfileComplete(state)
  })
}

const mapDispatchToProps = dispatch => ({
  fetchUser: () => dispatch(fetchUser())
})

export default connect(mapStateToProps, mapDispatchToProps)(DashboardView)



