import React from 'react'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import MyApplicationButtonsWeb from './MyApplicationButtons'
import MyApplicationButtonsMobile from './MyApplicationButtons.mobile'

const MyApplicationButtons = props => props.isMobile ? <MyApplicationButtonsMobile {...props} /> : <MyApplicationButtonsWeb {...props} />

export default withDeviceWidthDetector(MyApplicationButtons)



