import { connect } from 'react-redux'
import AdvertCampaign from 'Shared/components/AdvertCampaign/AdvertCampaign'
import { fetchActiveAdvertCampaign } from 'Shared/ducks/advertCampaign'
import { getAdvertBannerByType } from 'Shared/selectors/advertCampaign'

const mapStateToProps = (state, ownProps) => ({
  banner: getAdvertBannerByType(state, ownProps.type)
})

const mapDispatchToProps = dispatch => ({
  fetchActiveAdvertCampaign: () => dispatch(fetchActiveAdvertCampaign())
})

export default connect(mapStateToProps, mapDispatchToProps)(AdvertCampaign)



