import React, { Fragment } from 'react'
import './EmptyState.scss'

const EmptyStateMobile = ({ title, image }) => (
  <Fragment>
    <img src={image} alt='empty' className='job-offer-list__empty-state-image--mobile' />
    <div className='job-offer-list__empty-state-text--mobile'>{title}</div>
  </Fragment>
)

export default EmptyStateMobile



