import React from 'react'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import JobOfferListElementWeb from './JobOfferListElement'
import JobOfferListElementMobile from './JobOfferListElement.mobile'

const JobOfferListElement = props => props.isMobile ? <JobOfferListElementMobile {...props} /> : <JobOfferListElementWeb {...props} />

export default withDeviceWidthDetector(JobOfferListElement)



