import { connect } from 'react-redux'
import { submit, hasSubmitSucceeded, isSubmitting } from 'redux-form'
import { formName } from 'Candidate/connectors/Forms/Availability/Availability'
import { isStillAvailable } from 'Shared/helpers/date'
import CandidateDashboardNavbar from 'Candidate/views/Dashboard/components/CandidateDashboardNavbar'
import { fetchUser } from 'Shared/ducks/user/candidate'
import { isCandidateApproved, isCandidateRejected, isCandidateProfileIncomplete } from 'Shared/helpers/profile'

const mapStateToProps = state => ({
  profileCompletion: state.user.details.profileCompletionPercentage,
  isCandidateApproved: isCandidateApproved(state.user.details.profileCompletionStatus),
  isCandidateRejected: isCandidateRejected(state.user.details.profileCompletionStatus),
  isCandidateProfileIncomplete: isCandidateProfileIncomplete(state.user.details.profileCompletionStatus),
  hasSubmitSucceeded: hasSubmitSucceeded(formName)(state),
  isFormSubmitting: isSubmitting(formName)(state),
  isStillAvailable: isStillAvailable(state.user.details.availableTo),
  id: state.user.details.id
})

const mapDispatchToProps = dispatch => ({
  submitForm: () => dispatch(submit(formName)),
  fetchCandidate: id => dispatch(fetchUser(id))
})

export default connect(mapStateToProps, mapDispatchToProps)(CandidateDashboardNavbar)



