import React, { Component } from 'react'
import PropTypes from 'prop-types'
import withTranslation from 'Shared/hoc/withTranslations'
import MyApplicationsConnector from 'Candidate/connectors/MyApplications/MyApplications'
import { SwipeEntitiesListWrapper, SwipeEntitiesListTab } from 'Shared/components/SwipeEntitiesListTabs'
import './MyApplicationsView.scss'

class MyApplicationsViewMobile extends Component {
  state = {
    activeTab: this.props.applicationsTabs.applied,
    areApplicationsLoading: false
  }

  async componentDidMount () {
    this.setState({ areApplicationsLoading: true })
    await this.props.loadJobOffers()
    this.setState({ areApplicationsLoading: false })
  }

  changeActiveTab = (event, activeTab) => this.setState({ activeTab })

  render () {
    const { t, applicationsTabs, applied, viewed, interviewRequested, hired, declined } = this.props
    const { activeTab } = this.state

    return (
      <section>
        <div>
          <SwipeEntitiesListWrapper>
            <SwipeEntitiesListTab
              text={t('myApplications.applied.text')}
              badge={applied.length}
              onClick={this.changeActiveTab}
              name={applicationsTabs.applied}
              isActive={activeTab === applicationsTabs.applied}
            />
            <SwipeEntitiesListTab
              text={t('myApplications.viewed.text')}
              badge={viewed.length}
              onClick={this.changeActiveTab}
              name={applicationsTabs.viewed}
              isActive={activeTab === applicationsTabs.viewed}
            />
            <SwipeEntitiesListTab
              text={t('myApplications.interviewRequested.text')}
              badge={interviewRequested.length}
              onClick={this.changeActiveTab}
              name={applicationsTabs.interviewRequested}
              isActive={activeTab === applicationsTabs.interviewRequested}
            />
            <SwipeEntitiesListTab
              text={t('myApplications.hired.text')}
              badge={hired.length}
              onClick={this.changeActiveTab}
              name={applicationsTabs.hired}
              isActive={activeTab === applicationsTabs.hired}
            />
            <SwipeEntitiesListTab
              text={t('myApplications.declined.text')}
              badge={declined.length}
              onClick={this.changeActiveTab}
              name={applicationsTabs.declined}
              isActive={activeTab === applicationsTabs.declined}
            />
          </SwipeEntitiesListWrapper>
          <div className='job-offer-view'>
            <MyApplicationsConnector
              hidePrivacy
              areApplicationsLoading={this.state.areApplicationsLoading}
              jobOffersList={this.props[activeTab]}
              jobOfferListCategory={activeTab}
            />
          </div>
        </div>
      </section>
    )
  }
}

MyApplicationsViewMobile.propTypes = {
  loadJobOffers: PropTypes.func.isRequired
}

export default withTranslation(MyApplicationsViewMobile)



