import React from 'react'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import MyApplicationsViewWeb from './MyApplicationsView'
import MyApplicationsViewMobile from './MyApplicationsView.mobile'

const MyApplicationsView = props => props.isMobile ? <MyApplicationsViewMobile {...props} /> : <MyApplicationsViewWeb {...props} />

export default withDeviceWidthDetector(MyApplicationsView)



