import React, { PureComponent } from 'react'
import ReactPaginate from 'react-paginate'
import PropTypes from 'prop-types'
import cx from 'classnames'
import './Pagination.scss'

// TODO Consider to reuse this component styles for algolia pagination component
class Pagination extends PureComponent {
  render () {
    const nextLinkClassName = cx({
      'pagination__nav': true,
      'pagination__nav--next': true,
      'pagination__nav--disabled': this.props.pageCount < 1 || this.props.forcePage + 1 > this.props.pageCount
    })

    return (
      <ReactPaginate
        containerClassName='pagination__container'
        activeClassName='pagination__page--active'
        pageClassName='pagination__page'
        nextClassName={nextLinkClassName}
        previousClassName='pagination__nav pagination__nav--previous'
        disabledClassName='pagination__nav--disabled'
        nextLinkClassName='pagination__nav__link pagination__nav__link--next'
        previousLinkClassName='pagination__nav__link pagination__nav__link--previous'
        previousLabel={'‹'}
        nextLabel={'›'}
        {...this.props}
      />
    )
  }
}

Pagination.propTypes = {
  marginPagesDisplayed: PropTypes.number.isRequired,
  pageRangeDisplayed: PropTypes.number.isRequired,
  pageCount: PropTypes.number.isRequired,
  forcePage: PropTypes.number,
  onPageChange: PropTypes.func
}

export default Pagination



