import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import CheckoutForm from 'Shared/forms/Checkout/CheckoutForm'
import PaymentService from 'Shared/services/PaymentService'
import { fetchOlivPlusMembershipPlan } from 'Shared/ducks/membership'
import { t } from 'i18next'
import NotificationService from 'Shared/services/NotificationService'
import { fetchActiveReccuringCandidateTransaction } from 'Shared/ducks/transactions'

const paymentService = new PaymentService()

const mapDispatchToProps = (dispatch, ownProps) => ({
  handleCancel: () => ownProps.closeModal(),
  handleSubmitCheckout: async token => {
    try {
      await paymentService.createOlivPlusProductManualPayment(token, ownProps.transactionId)
      NotificationService.success(t('checkout.notifications.success'))
      dispatch(fetchOlivPlusMembershipPlan())
      dispatch(fetchActiveReccuringCandidateTransaction())
      return ownProps.closeModal()
    } catch (e) {
      console.error(e)
      window.Frames.render()
      return NotificationService.error(t('checkout.notifications.error'))
    }
  }
})

export default withRouter(connect(null, mapDispatchToProps)(CheckoutForm))



