import React from 'react'
import { Helmet } from 'react-helmet'
import PropTypes from 'prop-types'
import withTranslations from 'Shared/hoc/withTranslations'
import MyApplicationsConnector from 'Candidate/connectors/Views/MyApplicationsConnector'
import { PageTopHeaderMobile } from 'Shared/components/PageTopHeader'
import { cleanseString, withPossessiveS } from 'Shared/helpers/string'
import CandidateDashboardNavbarConnector from 'Candidate/connectors/DashboardView/CandidateDashboardNavbarConnector'
import FailedPaymentNotificationConnector from 'Candidate/connectors/FailedPaymentNotification/FailedPaymentNotificationConnector'
import UpdateSalaryExpectationNotificationConnector from 'Candidate/connectors/UpdateSalaryExpectationNotification/UpdateSalaryExpectationNotificationConnector'
import CareerproBanner from 'Shared/components/CareerproBanner/CareerproBanner'
import CareerProModal from 'Shared/components/CareerProModal/CareerProModal'

import './DashboardView.scss'
import mixpanel from 'mixpanel-browser'

class DashboardViewMobile extends React.Component {
  state = {
    modalSeen: false
  }

  componentDidMount () {
    this.props.fetchUser()
    mixpanel.track("Page View - Dashboard");
  }

  render () {
    const { t, firstName } = this.props
    return (
      <div className='dashboard-view__container parsing--hidden'>
        <PageTopHeaderMobile title={t('common.myApplications')} />
        <CandidateDashboardNavbarConnector />
        <Helmet>
          <title>{firstName && cleanseString(t('meta.candidate.title.dashboard', { firstName: withPossessiveS(firstName) }))}</title>
          <meta property='og:title' content={firstName && cleanseString(t('meta.candidate.title.dashboard', { firstName: withPossessiveS(firstName) }))} />
        </Helmet>
        <CareerProModal />
        <div className='dashboard-view-mobile__notification-container notifyContainer'>
          <FailedPaymentNotificationConnector />
          <UpdateSalaryExpectationNotificationConnector />
        </div>
        <div className='dashboard-view-mobile'>
          {/* Careerpro Ads Banner */}
          <CareerproBanner
            name='dashboardNotify'
            des='No more waiting around with CareerPro'
            arrowSubmitButton
            hideCloseButton
          />
          <MyApplicationsConnector />
        </div>
      </div>
    )
  }
}

DashboardViewMobile.propTypes = {
  isCandidateRejected: PropTypes.bool,
  isCandidateRated: PropTypes.bool,
  profileCompletion: PropTypes.number,
  userFetched: PropTypes.bool,
  isCandidateAvailable: PropTypes.bool
}

export default withTranslations(DashboardViewMobile)



