import React, { useState } from 'react'
import './FailedPaymentNotification.scss'
import Notification from 'Shared/components/Notification'
import { Trans } from 'react-i18next'
import Modal from 'Shared/components/Modal'
import { t } from 'i18next'
import OlivPlusManualCheckoutFormConnector from 'Candidate/connectors/Checkout/OlivPlusManualCheckoutFormConnector'

function FailedPaymentsNotification (props) {
  const [isModalVisible, setModalVisibility] = useState(false)

  const openModal = () => setModalVisibility(true)
  const closeModal = () => setModalVisibility(false)

  const notificationText = () => (
    <Trans i18nKey='notifications.candidate.failedPayment'>
      Uh oh! There was a problem processing your payment. Please update your <strong><u><span onClick={openModal} className='failed-payment-notification__btn'>credit card details</span></u></strong>.
    </Trans>
  )

  const manualPaymentModal = () => (
    <Modal
      className='failed-payment-notification__modal'
      title={t('modals.manualPayment.title')}
      showSubmit={false}
      showCancel={false}
      handleClose={closeModal}
      show={isModalVisible}
    >
      <OlivPlusManualCheckoutFormConnector hideHeader transactionId={props.transactionId} closeModal={closeModal} submitName={t('modals.manualPayment.cta')} />
    </Modal>
  )

  return props.hasFailedPayment ? (
    <div>
      <Notification
        messageText={notificationText()}
        iconClassName='icon-im-alert-circle-outline'
        className='failed-payment-notification'
        error
      />
      {manualPaymentModal()}
    </div>
  ) : <div />
}

export default FailedPaymentsNotification



