import React, { useState, useEffect } from 'react'
import Label from 'Shared/components/FormElements/Label'
import withTranslations from 'Shared/hoc/withTranslations'
import Button, { PrimaryButton } from 'Shared/components/Button'
import { pathOr, pipe, cond, propEq, always } from 'ramda'
import { THEME_COLORS, FONT_FAMILY } from 'Shared/constants/UI'
import cx from 'classnames'
import mastercardLogo from 'Shared/assets/images/logos/mastercard-logo.svg'
import visaLogo from 'Shared/assets/images/logos/visa_logo.svg'
import './CheckoutForm.scss'

const initPaymentFrame = () => window.Frames.init({
  publicKey: process.env.REACT_APP_CHECKOUT_PUBLIC_KEY,
  style: {
    base: {
      color: THEME_COLORS.VULCAN,
      fontFamily: FONT_FAMILY,
      fontWeight: 400,
      letterSpacing: '1px'
    },
    focus: {
      color: THEME_COLORS.VULCAN
    },
    valid: {
      color: THEME_COLORS.VULCAN
    },
    invalid: {
      color: THEME_COLORS.ERROR
    },
    placeholder: {
      base: {
        color: THEME_COLORS.IRON,
        fontFamily: FONT_FAMILY,
        fontWeight: 400,
        letterSpacing: '1px'
      },
      focus: {
        color: THEME_COLORS.IRON
      },
      valid: {
        color: THEME_COLORS.IRON
      },
      invalid: {
        color: THEME_COLORS.ERROR
      }
    }
  }
})

const handleEvent = (eventName, eventHandler) => window.Frames.addEventHandler(
  window.Frames.Events[eventName],
  eventHandler
)

const removeEvent = (eventName) => window.Frames.removeAllEventHandlers(
  window.Frames.Events[eventName]
)

const formFrames = {
  cardNumber: 'card-number-frame',
  expiryDate: 'expiry-date-frame',
  cvv: 'cvv-frame'
}

const formFields = {
  cardNumber: 'card-number',
  expiryDate: 'expiry-date',
  cvv: 'cvv'
}

function CheckoutForm (props) {
  const { t } = props
  const [cardNumberValidation, setCardNumberValidation] = useState({ isValid: true, isEmpty: true })
  const [expiryDateValidation, setExpiryDateValidation] = useState({ isValid: true, isEmpty: true })
  const [cvvValidation, setCvvValidation] = useState({ isValid: true, isEmpty: true })
  const [isCardValid, setIsCardValid] = useState(false)
  const [loading, setLoading] = useState(false)

  useEffect(() => {
    initPaymentFrame()
    handleEvent('FRAME_VALIDATION_CHANGED', onValidationChanged)
    handleEvent('CARD_VALIDATION_CHANGED', onCardValidationChanged)
    handleEvent('CARD_TOKENIZED', onCardTokenized)

    return () => {
      removeEvent('FRAME_VALIDATION_CHANGED')
      removeEvent('CARD_VALIDATION_CHANGED')
      removeEvent('CARD_TOKENIZED')
    }
  }, [])

  const setValidationMap = {
    [formFields.cardNumber]: {
      value: cardNumberValidation,
      setValue: setCardNumberValidation
    },
    [formFields.expiryDate]: {
      value: expiryDateValidation,
      setValue: setExpiryDateValidation
    },
    [formFields.cvv]: {
      value: cvvValidation,
      setValue: setCvvValidation
    }
  }

  const onCardValidationChanged = () => setIsCardValid(window.Frames.isCardValid())
  const onValidationChanged = event => setValidationMap[event.element].setValue({ isValid: event.isValid, isEmpty: event.isEmpty })
  const isFieldValid = fieldName => pathOr(true, [fieldName, 'value', 'isValid'])(setValidationMap)
  const getFieldLabel = fieldName => pipe(
    pathOr({ isValid: true, isEmpty: true }, [fieldName, 'value']),
    cond([
      [propEq('isValid', true), always(t(`checkout.labels.${fieldName}.valid`))],
      [propEq('isEmpty', true), always(t(`checkout.labels.${fieldName}.empty`))],
      [propEq('isValid', false), always(t(`checkout.labels.${fieldName}.invalid`))]
    ])
  )(setValidationMap)

  const getInputStyles = defaultStyle => fieldName => cx({
    [defaultStyle]: true,
    'checkout__input-container--error': !isFieldValid(fieldName)
  })

  const getLabelStyles = fieldName => cx({
    'checkout__input-label--error': !isFieldValid(fieldName)
  })

  const handleSubmit = e => {
    setLoading(true)
    e.preventDefault()
    return window.Frames.submitCard()
  }

  const onCardTokenized = async e => {
    await props.handleSubmitCheckout(e.token)
    return setLoading(false)
  }

  const getSubmitName = () => loading ? `${t('common.loading')}...` : props.submitName || t('checkout.makePayment')

  return (
    <div>
      <div className='checkout__header'>
        <div className='checkout__header-title'>{!props.hideHeader && t('checkout.formHeader')}</div>
        <div className='checkout__header-logos'>
          <img src={visaLogo} alt='Visa' />
          <img src={mastercardLogo} alt='Mastercard' />
        </div>
      </div>
      <form id='payment-form' autoComplete='off'>
        <div className='checkout__card-number-element'>
          <Label className={getLabelStyles(formFields.cardNumber)}>{getFieldLabel(formFields.cardNumber)}</Label>
          <div className={getInputStyles('checkout__input-container checkout__input-container--card-number')(formFields.cardNumber)}>
            <div className={formFrames.cardNumber} />
          </div>
        </div>
        <div className='checkout__date-and-code'>
          <div className='checkout__date-and-code-element'>
            <Label className={getLabelStyles(formFields.expiryDate)}>{getFieldLabel(formFields.expiryDate)}</Label>
            <div className={getInputStyles('checkout__input-container checkout__input-container--date')(formFields.expiryDate)}>
              <div className={formFrames.expiryDate} />
            </div>
          </div>
          <div className='checkout__date-and-code-element'>
            <Label className={getLabelStyles(formFields.cvv)}>{getFieldLabel(formFields.cvv)}</Label>
            <div className={getInputStyles('checkout__input-container checkout__input-container--code')(formFields.cvv)}>
              <div className={formFrames.cvv} />
            </div>
          </div>
        </div>
        <div className='checkout__buttons'>
          <Button onClick={props.handleCancel}>{t('common.cancel')}</Button>
          <PrimaryButton type='submit' onClick={handleSubmit} disabled={!isCardValid || loading} id='pay-button'>{getSubmitName()}</PrimaryButton>
        </div>
      </form>
    </div>
  )
}

export default withTranslations(CheckoutForm)



