/* eslint-disable camelcase */
import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom'
import cx from 'classnames'
import withTranslations from 'Shared/hoc/withTranslations'

import { THEME_COLORS } from 'Shared/constants/UI'
import { assessment } from 'Shared/constants/Rating'
import { ProfileCompletionStatuses } from 'Shared/constants/Profile'

import DashboardCard from 'Candidate/components/DashboardCard/DashboardCard'
import Label from 'Shared/components/FormElements/Label/Label'
import './ProfileDashboardRating.scss'

const { not_approved } = ProfileCompletionStatuses

const ProfileDashboardRating = ({ rating, t, profileCompletionStatus, isAutoApproved }) => {
  const ratingContent = rating => {
    return (
      <DashboardCard.Wrapper>
        <Label>{t(`rating.assessmentDashboard.${rating}.label`)}</Label>
        <div className='c-profile-dashboard-rating__text'>{t(`rating.assessmentDashboard.${rating}.description`)}</div>
      </DashboardCard.Wrapper>
    )
  }

  const ratingColor = {
    [not_approved]: THEME_COLORS.ERROR,
    [assessment.bad]: THEME_COLORS.ERROR,
    [assessment.average]: THEME_COLORS.WARNING,
    [assessment.good]: THEME_COLORS.LIGHT_GREEN
  }

  const iconClass = cx({
    'ion-md-star': profileCompletionStatus !== not_approved,
    'ion-md-close': profileCompletionStatus === not_approved
  })

  const buttonLink = () => (
    <Link to={'/candidate/profile/preview'} className='button button--outline button--small'>
      {t('common.seeRatingDetails')}
    </Link>
  )

  const getRatingContent = () => {
    switch (true) {
      case profileCompletionStatus === not_approved:
        return ratingContent(not_approved)
      case rating && profileCompletionStatus !== not_approved:
        return ratingContent(rating)
      default:
        return ratingContent(assessment.good)
    }
  }

  return (
    <DashboardCard>
      <DashboardCard.Icon icon={iconClass} color={ratingColor[rating || assessment.good]} />
      {getRatingContent()}
      {!isAutoApproved && buttonLink()}
    </DashboardCard>
  )
}

ProfileDashboardRating.propTypes = {
  rating: PropTypes.oneOf(Object.values(assessment)).isRequired
}

export default withTranslations(ProfileDashboardRating)



