import { createSelector } from 'reselect'
import { transformToArray } from 'Shared/helpers/redux'
import * as R from 'ramda'

const getAdvertBanners = state => state.advertCampaign.banners

export const getAdvertBannerByType = createSelector(
  getAdvertBanners,
  (state, type) => type,
  (banners, type) => R.pipe(
    transformToArray,
    R.filter(
      R.propEq('type', type)
    ),
    R.head
  )(banners)
)



