import React from 'react'
import withMobileDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import ProfileDashboardRejectedWeb from './ProfileDashboardRejected'
import ProfileDashboardRejectedMobile from './ProfileDashboardRejected.mobile'

const ProfileDashboardRejected = props => props.isMobile ? <ProfileDashboardRejectedMobile {...props} /> : <ProfileDashboardRejectedWeb {...props} />

export default withMobileDeviceWidthDetector(ProfileDashboardRejected)



