import { connect } from 'react-redux'
import { handleHidePostJobOfferAlert } from 'Shared/ducks/jobOffers'
import { getPostJobOfferAlertCode } from 'Shared/selectors/jobOffers'
import PublishJobOfferError from 'Employer/components/PublishJobOfferError/PublishJobOfferError'

const mapStateToProps = state => ({
  isVisible: state.jobOffers.postJobOfferAlertVisible,
  postJobOfferAlertCode: getPostJobOfferAlertCode(state)
})

const mapDispatchToProps = dispatch => ({
  handleHide: () => dispatch(handleHidePostJobOfferAlert())
})

export default connect(mapStateToProps, mapDispatchToProps)(PublishJobOfferError)



