import React from 'react'
import { connect } from 'react-redux'
import JobOffersList from 'Shared/components/JobOffersList/JobOffersList'
import Date from 'Shared/components/JobOfferListElement/components/Date'
import MyApplicationButtonsConnector from 'Candidate/connectors/MyApplications/MyApplicationButtonsConnector'
import { getApplicationByJobOfferId } from 'Shared/selectors/jobOfferApplications'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import { t } from 'i18next'
import moment from 'moment'
import { Roles } from 'Shared/constants/User'

const mapStateToProps = (state, ownProps) => ({
  isCandidate: state.user.role === Roles.candidate.name,
  withPagination: false,
  jobOfferCard: {
    hideExpirationDate: true,
    body: ownProps.isMobile ? null : jobOfferData => {
      const isPastDate = moment().diff(jobOfferData.expiresAt, 'days') >= 0

      return <Date
        date={jobOfferData.expiresAt}
        dangerDate={isPastDate}
        dateType={isPastDate ? t('common.expired') : t('jobOffer.jobDetails.deadline')}
      />
    },
    buttons: jobOfferData => (
      <MyApplicationButtonsConnector
        jobOfferId={jobOfferData.id}
        jobOfferTitle={jobOfferData.title}
        application={getApplicationByJobOfferId(state, jobOfferData.id)}
        jobOffer={jobOfferData}
      />
    )
  }
})

export default withDeviceWidthDetector(connect(mapStateToProps)(JobOffersList))



