import { connect } from 'react-redux'
import ProfileDashboardAvailability from 'Candidate/components/ProfileDashboardAvailability/ProfileDashboardAvailability'
import { isStillAvailable } from 'Shared/helpers/date'
import { profileSteps } from 'Shared/ducks/user/candidate'

const mapStateToProps = state => {
  const { availableFrom, availableTo } = state.user.details

  return ({
    availableFrom,
    availableTo,
    isCandidateAvailable: isStillAvailable(availableTo)
  })
}

const mapDispatchToProps = dispatch => ({
  profileSteps: () => dispatch(profileSteps())
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfileDashboardAvailability)



