import React from 'react'
import PropTypes from 'prop-types'
import { formats } from 'Shared/constants/Date'
import { formatDate } from 'Shared/helpers/date'
import cx from 'classnames'

const Date = ({ date, dateType, dangerDate }) => {
  const dateLabelClass = cx({
    'job-offer-card__label': true,
    'job-offer-card__date--danger': dangerDate
  })

  return (
    <div className={dateLabelClass}>{dateType}: { formatDate(date, formats.dashed) }</div>
  )
}

Date.propTypes = {
  date: PropTypes.string,
  dateType: PropTypes.string.isRequired,
  dangerDate: PropTypes.bool
}

export default Date



