import React, { Component } from 'react'
import cx from 'classnames'
import Button, { PrimaryButton } from 'Shared/components/Button'
import ProfileAvailabilityConnector from 'Candidate/connectors/Availability/ProfileAvailabilityConnector'
import AvailabilityFormConnector from 'Candidate/connectors/Forms/Availability/Availability'
import NotificationService from 'Shared/services/NotificationService'
import withTranslations from 'Shared/hoc/withTranslations'
import ProfileCompletionConnector from 'Candidate/connectors/ProfileCompletionConnector'
import ProfileDashboardRatingConnector from 'Candidate/connectors/ProfileDashboardRatingConnector'
import ProfileDashboardRejected from 'Candidate/components/ProfileDashboardRejected'
import './CandidateDashboardNavbar.scss'
import { PreviewModalMobile } from 'Shared/components/Modal'

class CandidateDashboardNavbar extends Component {
  state = {
    isAvailabilityPreviewOpen: false,
    isAvailabilityChangeOpen: false,
    isProfileCompletionPreviewOpen: false
  }

  componentDidMount () {
    this.props.fetchCandidate(this.props.id)
  }

  componentDidUpdate (prevProps) {
    if (this.props.hasSubmitSucceeded && prevProps.hasSubmitSucceeded !== this.props.hasSubmitSucceeded) {
      NotificationService.success(this.props.t('alerts.availabilityUpdated'))
      this.closeAvailabilityChange()
    }
  }

  getProfileNotification = () => {
    switch (true) {
      case this.props.isCandidateRejected:
        return <ProfileDashboardRejected />
      case this.props.isCandidateApproved:
        return <ProfileDashboardRatingConnector />
      default:
        return <ProfileCompletionConnector />
    }
  }

  openAvailabilityPreview = () => this.setState({ isAvailabilityPreviewOpen: true })
  closeAvailabilityPreview = () => this.setState({ isAvailabilityPreviewOpen: false })

  openProfileCompletionPreview = () => this.setState({ isProfileCompletionPreviewOpen: true })
  closeProfileCompletionPreview = () => this.setState({ isProfileCompletionPreviewOpen: false })

  openAvailabilityChange = () => {
    this.closeAvailabilityPreview()
    this.setState({ isAvailabilityChangeOpen: true })
  }
  closeAvailabilityChange = () => this.setState({ isAvailabilityChangeOpen: false })

  availabilityModal = () => {
    const { t } = this.props
    return <PreviewModalMobile
      title={t('profile.preferences.sections.availability.label')}
      show={this.state.isAvailabilityPreviewOpen}
      handleClose={this.closeAvailabilityPreview}
    >
      <ProfileAvailabilityConnector />
      <Button
        onClick={this.openAvailabilityChange}
        className='candidate-dashboard-navbar__modal-btn'
      >
        {t('common.changeDates')}
      </Button>
    </PreviewModalMobile>
  }

  changeAvailabilityModal = () => {
    const { t } = this.props
    return <PreviewModalMobile
      title={t('profile.preferences.sections.availability.label')}
      show={this.state.isAvailabilityChangeOpen}
      handleClose={this.closeAvailabilityChange}
    >
      <div className='candidate-dashboard-navbar__modal-text'>
        {t('profile.preferences.sections.availability.text')}
      </div>
      <AvailabilityFormConnector />
      <PrimaryButton
        disabled={this.props.isFormSubmitting}
        onClick={this.props.submitForm}
        className='candidate-dashboard-navbar__modal-submit-btn'
      >
        {t('common.saveChanges')}
      </PrimaryButton>
    </PreviewModalMobile>
  }

  profileCompletionPreviewModal = () => {
    const { t } = this.props
    return <PreviewModalMobile
      title={t('common.profileRating')}
      show={this.state.isProfileCompletionPreviewOpen}
      handleClose={this.closeProfileCompletionPreview}
    >
      {this.getProfileNotification()}
    </PreviewModalMobile>
  }

  render () {
    const calendarIconClass = cx({
      'icon-im-calendar candidate-dashboard-navbar__availability-btn': true,
      'candidate-dashboard-navbar__availability-btn--notification': !this.props.isStillAvailable
    })

    const profileIconClass = cx({
      'icon-im-account candidate-dashboard-navbar__profile-btn': true,
      'candidate-dashboard-navbar__profile-btn--notification': this.props.isCandidateProfileIncomplete
    })

    return (
      <div className='candidate-dashboard-navbar__container'>
        <i className={profileIconClass} onClick={this.openProfileCompletionPreview} />
        <i className={calendarIconClass} onClick={this.openAvailabilityPreview} />
        {this.state.isAvailabilityPreviewOpen && this.availabilityModal()}
        {this.state.isAvailabilityChangeOpen && this.changeAvailabilityModal()}
        {this.state.isProfileCompletionPreviewOpen && this.profileCompletionPreviewModal()}
      </div>
    )
  }
}

export default withTranslations(CandidateDashboardNavbar)



