import { connect } from 'react-redux'
import ProfileCompletion from 'Candidate/components/ProfileCompletion'
import { getFirstProfileUncompleteStep, isCandidateProfileComplete } from 'Shared/selectors/user'
import { profileSteps } from 'Shared/ducks/user/candidate'
import { applySpec, path } from 'ramda'

const mapStateToProps = applySpec({
  profileCompletion: path(['user', 'details', 'profileCompletionPercentage']),
  firstIncompleteStep: getFirstProfileUncompleteStep,
  isCandidateProfileComplete: isCandidateProfileComplete
})

const mapDispatchToProps = dispatch => ({
  profileSteps: () => dispatch(profileSteps())
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfileCompletion)



