import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import PropTypes from 'prop-types'
import { THEME_COLORS } from 'Shared/constants/UI'
import withTranslations from 'Shared/hoc/withTranslations'
import DashboardCard from 'Candidate/components/DashboardCard/DashboardCard'
import Notification from 'Shared/components/Notification'
import './ProfileCompletion.scss'

class ProfileCompletion extends Component {
  componentDidMount () {
    this.props.profileSteps()
  }

  getProgressWidth = () => {
    const { profileCompletion } = this.props

    return {
      width: `${profileCompletion}%`
    }
  }

  profileCompletionNotification = () => {
    const { t, isCandidateProfileComplete } = this.props

    return isCandidateProfileComplete && (
      <div className='profile-completion__complete-profile-notification-container'>
        <Notification
          messageText={t('notifications.candidate.approvedProfileVisibility')}
          iconClassName='icon-im-alert-circle-outline'
          warning
        />
      </div>
    )
  }

  get buttonLink () {
    const { profileCompletion, firstIncompleteStep, t } = this.props
    return profileCompletion === 100
      ? <Link to={'/candidate/profile/preview'} className='button button--outline button--small'>{t('common.viewProfile')}</Link>
      : <Link to={`/candidate/profile/complete/${firstIncompleteStep}`} className='button button--outline'>{t('common.updateProfile')}</Link>
  }

  render () {
    const { profileCompletion, t } = this.props
    return (
      <div className='profile-completion-mobile'>
        <DashboardCard.Icon icon='ion-md-person' color={THEME_COLORS.IRON} />
        <div className='profile-completion__wrapper--progress-bar'>
          <h4 className='profile-completion__heading profile-completion__heading--mobile'>{t('common.profileCompletion')} - <span>{profileCompletion}%</span></h4>
          <div className='profile-completion__progress-bar'>
            <div className='profile-completion__progress-bar-content' style={this.getProgressWidth()} />
          </div>
        </div>
        {this.profileCompletionNotification()}
        {this.buttonLink}
      </div>
    )
  }
}

ProfileCompletion.defaultProps = {
  profileCompletion: 0
}

ProfileCompletion.propTypes = {
  profileSteps: PropTypes.func.isRequired,
  profileCompletion: PropTypes.number.isRequired,
  firstIncompleteStep: PropTypes.string
}

export default withTranslations(ProfileCompletion)



