import React, { Fragment, PureComponent } from 'react'
import PropTypes from 'prop-types'
import { Link, withRouter } from 'react-router-dom'
import { Trans } from 'react-i18next'
import { compose } from 'redux'
import JobOfferListElementConnector from 'Shared/connectors/Components/JobOfferListElementConnector'
import withTranslations from 'Shared/hoc/withTranslations'
import { LIST_ITEMS_PER_PAGE } from 'Shared/constants/Global'
import { Statuses } from 'Shared/constants/Job'
import Pagination from 'Shared/components/Pagination'
import PublishJobOfferErrorConnector from 'Employer/connectors/PublishJobOfferError/PublishJobOfferErrorConnector'
import EmptyStateImageAppplied from 'Shared/assets/images/illustrations/empty-state-candidates-applied.svg'
import EmptyStateImageRest from 'Shared/assets/images/illustrations/empty-state-candidates-rest.svg'
import EmptyStateImageEmployers from 'Shared/assets/images/illustrations/employer-empty-state.svg'
import EmptyState from './components/EmptyState'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import { JobHitsLoader } from 'Shared/components/Main/MainLoaders'

import './JobOffersList.scss'
import Loader from 'Shared/components/Loader/Loader'
import { Helmet } from 'react-helmet'
import { cleanseString, withPossessiveS } from 'Shared/helpers/string'

class JobOffersList extends PureComponent {
  static propTypes = {
    jobOfferListCategory: PropTypes.string.isRequired,
    perPage: PropTypes.number,
    loadJobOffers: PropTypes.func,
    jobOffersList: PropTypes.array.isRequired,
    jobOfferCard: PropTypes.shape({
      body: PropTypes.func,
      buttons: PropTypes.func
    }),
    jobOfferPublishedIds: PropTypes.array
  }

  static defaultProps = {
    perPage: LIST_ITEMS_PER_PAGE,
    jobOfferListCategory: '',
    postAJobAccessAlertModalVisible: false,
    hidePostAJobAccessAlertModal: () => {},
    withPagination: true,
    loadJobOffers: () => {}
  }

  state = {
    offset: 0,
    isLoading: false
  }

  pageCount = 0

  handlePaginationClick = data => this.setState({ offset: data.selected })

  clearOffset = () => this.setState({ offset: 0 })

  async componentDidMount () {
    this.setState({ isLoading: true })
    await this.props.loadJobOffers()
    this.pageCount = this.props.jobOffersList.length / this.props.perPage
    const isNotADraft = this.props.jobOfferListCategory !== Statuses.draft
    const isAnEmployer = !this.props.isCandidate
    isAnEmployer && isNotADraft && this.props.getApplicationsCounters(this.props.jobOfferPublishedIds)
    this.setState({ isLoading: false })
  }

  componentWillUnmount () {
    this.clearOffset()
  }

  componentDidUpdate (prevProps) {
    if (prevProps.jobOfferListCategory !== this.props.jobOfferListCategory) {
      this.clearOffset()
    }
  }

  componentWillReceiveProps (nextProps) {
    if (nextProps.jobOffersList.length !== this.props.jobOffersList.length) {
      this.pageCount = nextProps.jobOffersList.length / this.props.perPage
    }
  }

  get applications () {
    const { jobOfferCard, isCandidate, jobOfferListCategory, hidePrivacy, jobOfferApplicationsCounters } = this.props

    const applicationsOffset = this.state.offset * this.props.perPage
    const paginated = this.props.jobOffersList.slice(applicationsOffset, applicationsOffset + this.props.perPage)

    return paginated.map((offer, index) => (
      <JobOfferListElementConnector
        showOrganisationName={isCandidate}
        jobOfferListCategory={jobOfferListCategory}
        hidePrivacy={hidePrivacy}
        key={index}
        jobOfferId={offer.id}
        {...jobOfferCard}
        jobOfferApplicationsCounters={jobOfferApplicationsCounters}
      />
    ))
  }

  getEmptyState = (tabName) => {
    const { t } = this.props
    switch (tabName) {
      case Statuses.active:
      case Statuses.draft:
      case Statuses.inactive:
        return <EmptyState title={t(`employer.jobOffers.emptyState.${tabName}`)} image={EmptyStateImageEmployers} />
      case 'applied':
        return <EmptyState
          title={
            <Trans i18nKey='employer.jobOffers.emptyState.applied'>
              <div>There are no live applications. </div>
              <div><strong><Link to='/jobs'>View job opportunities</Link></strong> to get started</div>
            </Trans>
          }
          image={EmptyStateImageAppplied}
        />
      case 'declined':
        return <EmptyState
          title={
            <Trans i18nKey='employer.jobOffers.emptyState.declined'>
              <div>There is nothing here yet.</div>
              <div>And that's a good thing.</div>
            </Trans>
          }
          image={EmptyStateImageRest}
        />
      default:
        return <EmptyState
          title={
            <Trans i18nKey='employer.jobOffers.emptyState.default'>
              <div>There is nothing here yet.</div>
              <div>Try applying to more jobs that are right for you or <strong><a target='_blank' href='https://academy.oliv.com/'>Oliv Academy</a></strong> to enhance your profile</div>
            </Trans>
          }
          image={EmptyStateImageRest}
        />
    }
  }

  renderContent = () => {
    const { t, jobOfferListCategory: tabName, jobOffersList, areApplicationsLoading, isMobile } = this.props
    const { isLoading } = this.state
    switch (true) {
      case isMobile && (isLoading || areApplicationsLoading):
        return <JobHitsLoader />
      case (isLoading || areApplicationsLoading):
        return <Loader description={t('common.loading')} className='job-offers-list__loader' />
      case jobOffersList.length === 0:
        return (
          <div className='job-offer-list__empty-state'>
            {this.getEmptyState(tabName)}
          </div>
        )
      default:
        return (
          <div>
            {this.applications}
            <PublishJobOfferErrorConnector />
            {
              this.props.withPagination && this.pageCount > 1 && (
                <Pagination
                  marginPagesDisplayed={3}
                  pageRangeDisplayed={5}
                  pageCount={this.pageCount}
                  onPageChange={this.handlePaginationClick}
                  forcePage={this.state.offset}
                />
              )
            }
          </div>
        )
    }
  }

  renderMetaTitle = () => {
    const { jobOfferListCategory: tabName, companyName, t } = this.props
    switch (tabName) {
      case 'active':
        return companyName && cleanseString(t('meta.employer.title.dashboard.active', { companyName: withPossessiveS(companyName.trim()) }))
      case 'draft':
        return companyName && cleanseString(t('meta.employer.title.dashboard.draft', { companyName: withPossessiveS(companyName.trim()) }))
      case 'inactive':
        return companyName && cleanseString(t('meta.employer.title.dashboard.inactive', { companyName: withPossessiveS(companyName.trim()) }))
    }
  }

  render () {
    return <Fragment>
      {this.props.match.path !== '/candidate/dashboard' &&
        <Helmet>
          <title>{this.renderMetaTitle()}</title>
          <meta property='og:title' content={this.renderMetaTitle()} />
        </Helmet>
      }
      {this.renderContent()}
    </Fragment>
  }
}

export default compose(
  withTranslations,
  withRouter,
  withDeviceWidthDetector
)(JobOffersList)



