import React, { Component, Fragment } from 'react'
import PropTypes from 'prop-types'
import { Element } from 'react-scroll'
import cx from 'classnames'
import { getEditorContentLength } from 'Shared/helpers/editor'
import withTranslations from 'Shared/hoc/withTranslations'
import Label from '../Label'
import 'react-quill/dist/quill.snow.css'
import ReactQuill from 'react-quill'
import { path } from 'ramda'
import { isArabicLayout } from 'Shared/helpers/layout'

let icons = ReactQuill.Quill.import('ui/icons')
icons['bold'] = null
icons['italic'] = null
icons['underline'] = null
icons['list'] = null

class TextEditor extends Component {
  static propTypes = {
    placeholder: PropTypes.string,
    label: PropTypes.string,
    limit: PropTypes.number,
    className: PropTypes.string,
    t: PropTypes.func,
    customMode: PropTypes.shape({
      onlyLink: PropTypes.bool
    })
  }

  static defaultProps = {
    customMode: {}
  }

  lengthWatcher = () => {
    const quill = this.quillRef.getEditor()
    const { limit } = this.props
    const quillText = quill.getText()
    const currentLength = quillText.replace(/\n/g, '').length
    const newLinesMatches = quillText.match(/\n/g)
    if (currentLength >= limit && !this.props.preventLengthCutting) {
      quill.deleteText(limit + newLinesMatches.length - 1, quill.getLength())
    }
  }

  componentDidMount () {
    const quill = this.quillRef.getEditor()
    if (isArabicLayout) {
      quill.format('direction', 'rtl')
      quill.format('align', 'right')
    }

    quill.on('text-change', this.lengthWatcher)

    // eslint-disable-next-line
    quill.clipboard.addMatcher(Node.ELEMENT_NODE, (node, delta) => {
      let ops = []
      delta.ops.forEach(op => {
        if (op.insert &&
          typeof op.insert === 'string' &&
          path([ 'attributes', 'list' ], op) &&
          path([ 'attributes', 'list' ], op) !== 'bullet') {
          ops.push({
            insert: op.insert
          })
        } else {
          ops.push(op)
        }
      })
      delta.ops = ops
      return delta
    })
  }

  componentWillUnmount () {
    const quill = this.quillRef.getEditor()
    quill.off('text-change', this.lengthWatcher)
  }

  constructor (props) {
    super(props)
    this.state = {
      quillState: props.input.value ? props.input.value : ''
    }
  }

  customToolbar = (withLink) => {
    const { onlyLink, hideToolbar } = this.props.customMode
    let customOptions = null

    switch (true) {
      case hideToolbar: {
        return <span id={`toolbar-${this.uniqId}`} className={'form__editor-toolbar-hidden'} />
      }
      case onlyLink:
        customOptions = (
          <div className='quill-icon-container'>
            <button className='ql-link form__editor-toolbar-icon' type='button' />
          </div>
        )
        break
      default:
        customOptions = (
          <Fragment>
            <div className='quill-icon-container'>
              <button className='ql-bold form__editor-toolbar-icon icon-im-format-bold' type='button' />
              <button className='ql-italic form__editor-toolbar-icon icon-im-format-italic' type='button' />
              <button className='ql-underline form__editor-toolbar-icon icon-im-format-underlined' type='button' />
            </div>
            <div className='quill-icon-container quill-icon-container-with-margin'>
              <button
                className='ql-list form__editor-toolbar-icon form__editor-toolbar-icon--separated icon-im-format-unordered-list'
                type='button'
              />
              {withLink && <button className='ql-link form__editor-toolbar-icon' type='button' />}
            </div>
          </Fragment>
        )
        break
    }

    return (
      <div id={`toolbar-${this.uniqId}`} className='form__editor-toolbar'>
        {customOptions}
      </div>
    )
  }

  componentWillReceiveProps (nextProps) {
    if (nextProps.input.value !== '') {
      this.setState({ quillState: nextProps.input.value })
    }
  }

  onChange = (quillState) => {
    const { onChange } = this.props.input

    this.setState({ quillState })
    onChange(quillState)
  }

  handleQuillBlur = (range, source, editor) => {
    const { input } = this.props
    setTimeout(() => {
      const fixRange = editor.getSelection()
      if (fixRange) {
        let editorInstance = this.quillRef.getEditor()
        editorInstance.setContents(editorInstance.getContents())
        editorInstance.setSelection(fixRange)
      } else {
        input.onBlur(input.value)
      }
    }, 100)
  }

  uniqId = Math.random().toString(36).slice(5)

  modules = {
    toolbar: { container: `#toolbar-${this.uniqId}` },
    clipboard: { matchVisual: false }
  }

  render () {
    const { input, label, placeholder, limit, meta: { touched, error, submitting }, className, t, disabled, withLink, customMode } = this.props
    const { quillState } = this.state
    const contentLength = getEditorContentLength(input.value)

    const fieldClass = cx({
      'form_editor--error': !submitting && touched && error,
      [className]: !!className
    })

    let formats = null
    const linkFormat = withLink ? 'link' : ''

    switch (true) {
      case customMode.onlyLink:
        formats = ['link']
        break
      default:
        formats = ['bold', 'italic', 'underline', 'list', 'background', linkFormat]
    }

    return (
      <div className={fieldClass}>
        {input.name && <Element name={`error-position-${input.name}`} />}
        <div>
          <Label>
            <span className='form__editor-description'>{touched && error ? t(error.key, error.options) : label}</span>
            {limit && <span className='form__editor-counter'>{contentLength}/{limit}</span>}
          </Label>
        </div>
        {this.customToolbar(withLink)}
        <ReactQuill
          value={quillState}
          className='form__editor'
          readOnly={disabled}
          placeholder={placeholder}
          formats={formats}
          modules={this.modules}
          onChange={this.onChange}
          ref={element => { this.quillRef = element }}
          id={this.uniqId}
          onBlur={this.handleQuillBlur}
        />
      </div>
    )
  }
}

export default withTranslations(TextEditor)



